import React, { useState, useEffect } from 'react'
import {
  CInputGroup,
  CFormInput,
  CRow,
  CCol,
  CButton,
  CAlert,
  CAlertHeading,
  CContainer,
  CSpinner,
} from '@coreui/react'
import CIcon from '@coreui/icons-react'
import { cilCheckCircle } from '@coreui/icons'
import * as OPTIONS from '@utils/setting'
import { saveProductVersion, uploadProductFile, getProductVersions } from '@service/api'
import useApi from '@service/useApi'
import InfoModal from '@views/Share/InfoModal'

const defaultFileInfo = {
  faculty: { fileName: '', load: false, excelData: null },
  mvlp: { fileName: '', load: false, excelData: null },
  mpsa: { fileName: '', load: false, excelData: null },
  server_and_cloud_enrollment: {
    fileName: '',
    load: false,
    excelData: null,
  },
  student: { fileName: '', load: false, excelData: null },
  additional_product: { fileName: '', load: false, excelData: null },
  enterprise: { fileName: '', load: false, excelData: null },
}
const defaultUpload = {
  faculty: 0,
  mvlp: 0,
  mpsa: 0,
  server_and_cloud_enrollment: 0,
  student: 0,
  additional_product: 0,
  enterprise: 0,
}
const ImportItemData = () => {
  const { request, data, loading, error } = useApi(saveProductVersion)
  const uploadProductFileApi = useApi(uploadProductFile)
  const getProductVersionsApi = useApi(getProductVersions)
  const [version, setVersion] = useState({ currentVersion: '', latestVersion: '' })
  const uploadRef = React.useRef([])
  const [fileInfo, setFileInfo] = useState(defaultFileInfo)
  const [isUpload, setUpload] = useState(defaultUpload)
  const [isAllDone, setAllDone] = useState(false)
  const [isShowModal, setShowModal] = useState(false)
  const [importFiles, setImportFiles] = useState([])

  let arrLength = OPTIONS.CAGETORY_OPTIONS
  if (uploadRef.current.length !== arrLength.length) {
    uploadRef.current = Array(arrLength).map(
      (item, i) => uploadRef.current[item] || React.createRef(),
    )
  }

  useEffect(() => {
    getProductVersionsApi.request()
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  const uploadData = (id, i) => {
    isUpload[id] = 1
    setUpload({ ...isUpload })

    if (fileInfo[id]?.excelData) {
      let formData = new FormData()
      formData.append('file', fileInfo[id].excelData)

      uploadProductFileApi
        .request(formData.getAll('file'))
        .then((res) => {
          if (res.status === 200) {
            isUpload[id] = 2
            setUpload({ ...isUpload })
            // eslint-disable-next-line array-callback-return
            var allUploadDone = Object.keys(isUpload).every((i) => {
              return isUpload[i] === 2
            })
            if (allUploadDone) {
              setAllDone(true)
            }
          }
        })
        .catch((error) => {
          isUpload[id] = 0
          removeFile(id, i)
          console.log(error)
        })
    }
  }

  const removeFile = (id, i) => {
    uploadRef.current[i].value = null
    fileInfo[id].excelData = null
    setFileInfo({ ...fileInfo })
  }
  const handleChange = (e, id) => {
    const files = e.target.files
    fileInfo[id].fileName = files[0].name
    fileInfo[id].excelData = files[0]
    setFileInfo({ ...fileInfo })
    handleFile(files[0], id)
  }
  const handleFile = (file, id) => {
    if (!file) {
      console.error('file is empty')
      return
    } else {
      fileInfo[id].load = true
      setFileInfo({ ...fileInfo })
    }
  }

  const refactorData = () => {
    for (const [key, value] of Object.entries(fileInfo)) {
      let file = { fileName: value.fileName, category: key }
      importFiles.push(file)
    }
    setImportFiles(importFiles)
  }
  const handleSaveVersion = (items) => {
    refactorData()
    // 清空uploadRef
    Array(items).map((_, i) => (uploadRef.current[i].value = null))

    request({
      userName: 'admin',
      description: '',
      importFiles: importFiles,
    })
  }

  useEffect(() => {
    if (data || error) {
      // 清空資料
      setImportFiles([])
      setFileInfo(defaultFileInfo)
      setUpload(defaultUpload)
      setAllDone(false)
    }
    if (error) setShowModal(true)

    if (getProductVersionsApi.data?.status === 200) {
      setVersion({
        currentVersion: {
          num: getProductVersionsApi.data?.data?.currentVersionData?.versionNumber,
          status: getProductVersionsApi.data?.data?.currentVersionData?.statusDisplay,
        },
        latestVersion: {
          num: getProductVersionsApi.data?.data?.latestVersionData?.versionNumber,
          status: getProductVersionsApi.data?.data?.latestVersionData?.statusDisplay,
        },
      })
    }
    if (getProductVersionsApi.error) {
      setVersion({
        currentVersion: '系統忙碌，請稍後再試',
        latestVersion: '系統忙碌，請稍後再試',
      })
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [data, error, getProductVersionsApi.data, getProductVersionsApi.error])

  // eslint-disable-next-line react/prop-types
  const CheckComponent = ({ show }) => {
    // eslint-disable-next-line react/prop-types
    return show ? (
      <CCol md={2} className="d-flex align-items-end mb-4">
        <CIcon
          className={show === 1 ? 'text-secondary' : 'text-success'}
          icon={cilCheckCircle}
          size="lg"
        />
      </CCol>
    ) : null
  }

  // eslint-disable-next-line react/prop-types
  const RenderFileUploadRow = ({ categoryOptions, num }) => {
    // eslint-disable-next-line react/prop-types
    const { displayName, id } = categoryOptions
    return (
      <CRow>
        <CCol md={3}>
          <CFormInput type="text" value={displayName} readOnly={true} disabled={true} />
        </CCol>
        <CCol md={6}>
          <CInputGroup className="mb-4">
            <label className="inputFile">
              <div>請選擇檔案</div>
              <CFormInput
                accept=".xlsx"
                id={id}
                type="file"
                onChange={(e) => {
                  handleChange(e, id)
                }}
                ref={(id) => (uploadRef.current[num] = id)}
                style={{ display: 'none' }}
              />
            </label>
            <CFormInput
              readOnly={true}
              disabled={true}
              type="text"
              value={fileInfo[id].fileName || '未選擇任何檔案'}
              ref={(id) => (uploadRef.current[num] = id)}
              style={{ backgroundColor: 'transparent' }}
            />
            <CButton
              type="button"
              color={!fileInfo[id].load ? 'secondary' : 'primary'}
              variant="outline"
              disabled={!fileInfo[id].load}
              onClick={() => uploadData(id, num)}
            >
              開始上傳
            </CButton>
          </CInputGroup>
        </CCol>
        <CheckComponent show={isUpload[id]} />
      </CRow>
    )
  }

  return (
    <div className="px-3">
      <CContainer>
        {data?.status === 200 && (
          <CAlert color="success" dismissible>
            上傳成功
          </CAlert>
        )}
        {!isAllDone && (
          <CAlert color="info" dismissible>
            <CAlertHeading component="h4">MS檟格冊整批匯入</CAlertHeading>
            <p className="m-0">1. 需上傳全部的價格冊才能整批匯入</p>
            <p className="m-0">2. 價格冊檔名不可有中文</p>
          </CAlert>
        )}
        <h5 className="mb-3">價格冊資訊</h5>
        <div className="form-box">
          <CRow className="align-items-center pb-2">
            <CCol sm={4}>當前價格冊版號({version.currentVersion.status})</CCol>
            <CCol sm={2}>{version.currentVersion.num}</CCol>
          </CRow>
          <CRow className="align-items-center">
            <CCol sm={4}>最新價格冊版本({version.latestVersion.status})</CCol>
            <CCol sm={2}>{version.latestVersion.num}</CCol>
          </CRow>
        </div>
        <h5 className="mb-3">上傳價格冊</h5>
        <div className="form-box mb-1 pt-4 pb-0" style={{ borderRadius: '10px 10px 0 0' }}>
          {OPTIONS.CAGETORY_OPTIONS.map((item, index) => {
            return <RenderFileUploadRow categoryOptions={item} key={index} num={index} />
          })}
        </div>
        <div className="form-box" style={{ borderRadius: '0 0 10px 10px' }}>
          <CRow md={{ cols: 12 }}>
            <CCol md={12}>
              <CButton
                onClick={() => handleSaveVersion(OPTIONS.CAGETORY_OPTIONS)}
                disabled={!isAllDone || loading}
                color={!isAllDone ? 'secondary' : 'primary'}
                type="button"
                variant="outline"
              >
                {loading ? (
                  <>
                    <CSpinner component="span" size="sm" aria-hidden="true" />
                    <span> Loading...</span>
                  </>
                ) : (
                  '送出價格冊'
                )}
              </CButton>
            </CCol>
          </CRow>
        </div>
      </CContainer>
      <InfoModal
        isShow={isShowModal}
        closeModal={() => setShowModal(false)}
        title="系統錯誤"
        infoContain="系統忙碌，請稍後再試"
      />
    </div>
  )
}

export default ImportItemData
