import React, { useState, useEffect } from 'react'
import { CButton, CModal, CModalHeader, CModalTitle, CModalFooter, CModalBody } from '@coreui/react'
import PropTypes from 'prop-types'
const InfoModal = (props) => {
  const [visible, setVisible] = useState(false)
  useEffect(() => {
    setVisible(props.isShow)
  }, [props.isShow])

  return (
    <>
      <CModal alignment="center" visible={visible} onClose={props.closeModal}>
        <CModalHeader>
          <CModalTitle>{props.title || ''}</CModalTitle>
        </CModalHeader>
        <CModalBody>{props.infoContain}</CModalBody>
        <CModalFooter>
          <CButton color="primary" onClick={props.closeModal}>
            我知道了
          </CButton>
        </CModalFooter>
      </CModal>
    </>
  )
}

InfoModal.propTypes = {
  title: PropTypes.string,
  isShow: PropTypes.bool,
  closeModal: PropTypes.func,
  infoContain: PropTypes.string,
}

export default InfoModal
